@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
}

.input {
  @include input($colorDarkest);

  padding: 2rem;
  width: 100%;
  line-height: 1;
  text-align: center;
  background-color: transparent;
  border: none;
  border-bottom: 3px solid $colorHighlight;

  &::placeholder {
    color: rgba(0, 0, 0, 0.3);
  }
}

.icon {
  position: absolute;
  bottom: 0.3rem;
  right: -4rem;
  width: 4rem;
}

.error {
  @include error;

  position: absolute;
  bottom: -1.5rem;
  left: 0;
  display: block;
  width: 100%;
  text-align: center;
}

@media (max-width: $adaptiveMax) {
  .input {
    padding: 0.6rem 4rem;
    border-bottom: 2px solid $colorHighlight;
  }

  .icon {
    bottom: 0.6rem;
    right: 0;
    width: 2.7rem;
  }

  .error {
    bottom: -1rem;
  }
}
