@import "styles/variables";
@import "styles/mixins";

.form {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem 6rem 12rem 6rem;
  min-height: 49rem;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 70%;
    background-color: $colorMedium;
    border-radius: 3rem;
  }

  & > * {
    position: relative;
    z-index: 1;
  }
}

.checkWrapper {
  @include regular($colorDarkest);

  display: flex;
  align-items: center;
  margin-top: 3rem;
}

.checked {
  margin-right: 1.4rem;
}

.buttonAccept {
  @include resetButton;
  @include regular($colorDarkest);

  text-decoration: underline;
}

.button,
.buttonRepeat {
  height: 6rem;
}

.button {
  @include descriptionPromo($colorLightest, 400);

  margin-top: 4rem;
  width: 20rem;
}

.iconSubmitted {
  margin-top: 2rem;
  width: 7rem;
}

.confirmation {
  @include input;

  margin-top: 1.5rem;
  text-align: center;
}

.description {
  @include nav($colorDarkest, 400);

  margin-top: 2.5rem;
  text-align: center;
}

.buttonRepeat {
  margin-top: 9rem;
}

@media (max-width: $adaptiveMax) {
  .form {
    padding: 1.7rem 1.7rem 15rem 1.7rem;
    min-height: 35rem;

    &::before {
      height: 19rem;
    }
  }

  .input {
    &:not(:first-child) {
      margin-top: 1rem;
    }
  }

  .checkWrapper,
  .buttonAccept {
    font-size: 1rem;
    line-height: 1.2rem;
  }

  .checkWrapper {
    margin-top: 2rem;
  }

  .checked {
    margin-left: 2.8rem;
  }

  .button,
  .buttonRepeat {
    height: 5rem;
  }

  .button {
    margin-top: 3rem;
    width: 16rem;
  }

  .iconSubmitted {
    margin-top: 1rem;
    width: 5.4rem;
  }

  .confirmation {
    margin-top: 1rem;
  }

  .description {
    margin-top: 1rem;
  }

  .buttonRepeat {
    margin-top: 2.5rem;
  }
}
