@import "styles/variables";
@import "styles/mixins";

.wyziwig {
  padding-right: 4rem;
  padding-left: 4rem;
  width: 80rem;
  counter-reset: level1;

  & > div {
    margin-top: 3rem;
    padding-right: 4rem;
    max-height: 50rem;
    overflow: auto;
    scrollbar-width: thin;
    scrollbar-color: $colorMediumDarker transparent;

    &::-webkit-scrollbar {
      width: 0.5rem;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $colorMediumDarker;
    }

    & > p:first-child {
      margin-top: 0;
    }
  }


  h3 {
    @include h3;

    margin: 0;
  }

  p {
    @include regular($colorDarkest);

    &:first-of-type {
      margin-top: 3rem;
    }

    &:not(:first-of-type) {
      margin-top: 1.5rem;
    }
  }

  ol,
  ul {
    @include resetList;
  }

  ol {
    counter-reset: section;
    list-style-type: none;
  }

  ol > li {
    &::before {
      counter-increment: section;
      content: counters(section,".") " ";
    }
  }

  li {
    @include regular($colorDarkest);

    margin-top: 1rem;
  }
}

@media (max-width: $adaptiveMax) {
  .wyziwig {
    padding-right: 2rem;
    padding-left: 2rem;
    width: 90vw;

    & > div {
      padding-right: 2rem;
      max-height: 50vh;
    }
  }
}